var actions={
    changeTitle({commit},n){
        commit('change',n)
    },
    addList({commit},n){
        commit('addList',n)
    },
    addImg({commit},n){
        commit('addImg',n)
    },
    hash({commit},n){
        commit('hash',n)
    },
    musicMs({commit},n){
        commit('musicMs',n)
    },
    songNames({commit},n){
        commit('songNames',n)
    },
    shang({commit},n){
        commit('shangs',n)
    },
    qie1({commit}){
        commit('qie1')
    },
    qie2({commit}){
        commit('qie2')
    },
    isok1({commit}){
        commit('isok1')
    },
    isok2({commit}){
        commit('isok2')
    },
    // xun({commit}){
    //     commit('xun')
    // },
    sui({commit}){
        commit('sui')
    }
}
export default actions


