import VUeRouter from 'vue-router'
import Vue from 'vue'

import Pai from '../components/paihangbang.vue'
import Sou from '../components/sousuo.vue'
import Wd from '../components/wd.vue'
Vue.use(VUeRouter)
var routes=[
    {path:'/pai',component:Pai},
    {path:'/sou',component:Sou},
    {path:'/wd',component:Wd},
    {path:'*',redirect:'pai'}
]
var router = new VUeRouter({
    routes:routes
})

export default router;


