<template>
    <div class='play'>
    <img :src='urlM'>
    <marquee>{{songName}}</marquee>
    <audio id="au" autoplay :src="musicM" style="display:none"></audio>
    <i class="iconfont" @click="$store.dispatch('qie1')">&#xe64d;</i>
    <i class="iconfont" @click="$store.dispatch('isok1')" v-show="isok">&#xf004c;</i>
    <i class="iconfont" @click="$store.dispatch('isok2')" v-show="!isok">&#xf0067;</i>
    <i class="iconfont" @click="$store.dispatch('qie2')">&#xe64e;</i>
    <i class="iconfont" @click="xun">&#xe65c;</i>
    <i class="iconfont" @click="$store.dispatch('sui')">&#xe504;</i>
    </div>
</template>

<script>
    import {mapState,mapMutations} from "vuex"
    export default {
        methods:{
            xun(){
                var Ms=document.getElementById("au");
                Ms.loop();
            }
        },
        watch:{
            hashM:function(){
                console.log(this)
                this.$http.get("http://127.0.0.1/music/music.php",{
                    params:{
                        hash: this.hashM

                    }
                }).then(function (res){
                    // console.log(res.data)
                    this.$store.commit("addImg",res.data.imgUrl);
                    this.$store.commit('musicMs',res.data.url);
                    this.$store.commit("songNames",res.data.songName)
                }.bind(this))
            
            }
        },
        computed:mapState([
            "urlM",
            'hashM',
            'musicM',
            'songName',
            'isok',
            'num',
            'sui'
        ])
    }
</script>


