<template>
    <div>
        <loading-vue v-show="mList.length==0"></loading-vue>
        <ul>
            <li v-for='(key,index) in mList' @click="$store.commit('hash',index)" :class="{active:index==num}">
                <a>{{index+1}}.</a>
                <span>{{key.filename}}</span>
            </li>
            <p @click='jia' v-show="mList.length!=0">加载更多..</p>
        </ul>
        <paly-vue v-show="hashM!=''"></paly-vue>
    </div>
</template>

<script>
import PalyVue from './Paly.vue'
import {mapState} from 'vuex'
    export default {
        methods:{
            jia(){
                alert(1)
            }
        },
        components:{
            "paly-vue":PalyVue
        },
        mounted: function() {
            this.$http.get("http://127.0.0.1/music/kugou.php", {
              params:{
                page:1
              }
            }).then(function(res) {
                // console.log(res.data)
                this.$store.commit("addList",res.data.songs.list)
            }.bind(this))
        },
        computed:{
          ...mapState([
            'mList',
            'hashM',
            'num'
          ])
        }
    }
</script>


