<template>
    <header>{{title}}</header>
</template>
<script>
    import {mapState} from 'vuex'
    export default{
        computed:mapState([
            'title'
        ]),
        watch:{
            "$route" (to,from,next) {
                if(to.path=='/pai'){
                    this.$store.dispatch('changeTitle','排行歌曲')
                }else if(to.path=='/sou'){
                    this.$store.dispatch('changeTitle','搜索')
                }else{
                    this.$store.dispatch('changeTitle','我的')
                }
            }
        }
    }
</script>


