var mutations={
    change(state,name){
        state.title=name
    },
    addList(state,n){
        state.mList=n
    },
    addImg(state,url){
        state.urlM=url.replace('{size}/','')
        // console.log(state.urlM)
    },
    hash(state,index){
        state.hashM=state.mList[index].hash;
        state.num = index
        // console.log(state.hashM)
    },
    musicMs(state,url){
        state.musicM=url
    },
    songNames(state,url){
        state.songName=url
    },
    qie1(state){
        state.num--
        state.hashM=state.mList[state.num].hash;
    },
    qie2(state){
        state.num++
        state.hashM=state.mList[state.num].hash;
    },
    isok1(state){
        state.isok=!state.isok
        var audio =document.getElementById('au')
        audio.play()
    },
    isok2(state){
        state.isok=!state.isok
        var audio =document.getElementById('au')
        audio.pause()
    },
    // xun(state){
    //     console.log(state.num)
    //     state.hashM=state.mList[state.num].hash;
    // },
    sui(state){
        state.num=[Math.floor(Math.random()*29)] 
        console.log(state.num)
        state.hashM=state.mList[state.num].hash;
    }
}

export default mutations


